<script setup lang="ts">
import useAsync from "~/composables/use-async";
import TicketAPI from "~/services/ppv-ticket";
import { getPriceLabel } from "~/utils/subscription";

const { t } = useI18n();
const route = useRoute();
const {
  data: ticket,
  loading: loadingPPVTicket,
  error,
  run: loadUserTicket,
} = useAsync(() => TicketAPI.loadUserTicketById(route.params.id as string));

onMounted(() => loadUserTicket());
watch(error, () => showError({ statusCode: 404, data: { context: "page" } }));
</script>

<template>
  <div>
    <CardPPVDetailSkeleton v-if="loadingPPVTicket" />

    <CardPPVDetail
      v-else-if="ticket"
      :title="ticket.title"
      :price="getPriceLabel(ticket.price, ticket.currency)"
    />

    <div v-if="loadingPPVTicket" class="mt-5">
      <CardEventSkeleton />
    </div>
    <div v-else-if="ticket?.event" class="mt-5">
      <div class="text-secondary mb-5">
        {{ t("pages.account.tab_ppv_access_text") }}
      </div>

      <UserCardEvent :item="ticket.event" has-bookmark class="cardEvent" />
    </div>

    <div class="mt-8 text-center">
      <BaseButton
        tag="nuxt-link"
        to="/account/ppv/"
        type="secondary"
        name="billing-tab"
        link
      >
        {{ t("pages.account_subscription.button_back_to_my_tickets") }}
      </BaseButton>
    </div>
  </div>
</template>

<style scoped>
.cardEvent :deep(.cardEvent__image) {
  max-height: 18rem !important;
}

.cardEventSkeleton :deep(.cardEventSkeleton__image) {
    @apply w-full min-h-[18rem];
}
</style>
